<style scoped lang='scss'>
// Const
$cRed: #EF3527;
$cBlack: #252525;
$cYellow: #FFA630;
$cWhite: #FFFFFF;
$cGrey: rgba(55, 57, 46, .5);

//
.icons {
  padding: 40px 0;
  background-color: $cWhite;
  @media (max-width: 997.98px) {
    padding: 40px 0 10px 0;
  }
  .row {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    flex: 0 0 auto;
    .col-3 {
      flex: 0 0 25%;
      max-width: 25%;
      overflow: hidden;
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      @media (max-width: 997.98px) {
        flex: 0 0 50%;
        max-width: 50%;
        margin-bottom: 30px;
      }
      svg {
        max-width: 58px;
        max-height: 58px;
        object-fit: contain;
        object-position: center;
        path {
          animation-name: icons;
          animation-iteration-count: infinite;
          animation-duration: 5s;

        }
      }
      &:nth-child(4n - 1) {
        svg path {
          animation-delay: 4s;
        }
      }
      &:nth-child(4n - 2) {
        svg path {
          animation-delay: 2s;
        }
      }
      &:nth-child(4n - 3) {
        svg path {
          animation-delay: 3s;
        }
      }
      &:nth-child(4n - 4) {
        svg path {
          animation-delay: 1s;
        }
      }
    }
  }
}
@keyframes icons {
  0% {
    fill: $cGrey;
  }
  50% {
    fill: $cRed;
  }
  100% {
    fill: $cGrey;
  }
}
</style>
